import {Button, Col, Row} from "antd";
import React from "react";
import {useDispatch, useSelector} from "react-redux";
import "./BlackList.css";
import BlackListItem from "../BlackListItem/BlackListItem";
import {useNavigate} from "react-router-dom";

const BlackList = (props) => {

    const roles = useSelector((state) => state.roles);
    const dispatch = useDispatch()

    let navigate = useNavigate();

    const toggleRoleBan = id => {
        dispatch({ type: 'setRoleBan', id});
    };

    const rolesMarkUp = roles.map((role, index) => {
        return (
            <BlackListItem key={index} role={role} toggleRoleBan={toggleRoleBan}/>
        )
    })

    const onBanBtnClick = () => {
        navigate("../fool-picker", { replace: true });
    }

    return (
        <div className="container container--main">
            <Row justify="center">
                <Col align="center">
                    <h1>Забанить роли</h1>
                </Col>
            </Row>
            <Row justify="center">
                <Col align="center">
                    <div className={"blacklist"}>
                        {rolesMarkUp}
                    </div>
                    <Button type="primary" size="large" onClick={onBanBtnClick}>Забанить роли</Button>
                </Col>
            </Row>

        </div>
    )

}

export default BlackList;