import React from "react";


const BlackListItem = (props) => {

    const onRoleClick = () => {
        props.toggleRoleBan(props.role.id);
    }

    return (
        <div className={`blacklist__item ${props.role.isBanned && "blacklist__item--banned"}`} onClick={onRoleClick}>
            <div className={"blacklist__item-icon"}>
                <img alt="" src={`${process.env.PUBLIC_URL}/img/${props.role.icon}`} />
            </div>
            <div className={"blacklist__item-name"}>
                {props.role.name}
            </div>
        </div>
    )
}

export default BlackListItem;